*{
  margin:0;
}

body{
  background-color: #f5f6fa;
}

.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__left{
  flex: 0.9;
}

 .app__header{
  display: flex;  /*display in a row*/
  align-items:center;
  margin-bottom: 20px;
  justify-content: space-between;

}

.app__stats{
  display:flex;
  justify-content:space-between;
}


/* When the screen goes beyond 990 px then apply the functionality */
@media(max-width: 990px) {   
  .app{
    flex-direction:column;
  }
}